<template>
	<div class="page search">
		<von-header theme="dark">
			<span @click="back" slot="left"><i class="icon ion-chevron-left"></i><span class="dongtai"></span></span>
			<span slot="title"></span>
			<span @click="actionSheet" slot='right'><i class="icon ion-more"></i></span>
		</von-header>
		<div class="inp">
			<input type="text" placeholder="" name="" id="" value="" />
			<div @click="back"></div>
		</div>
		<div class="lists">
			<div>
				<div></div>
				<div></div>
				<div></div>
				<div></div>
				<div></div>
				<div></div>
				<div></div>
				<div></div>
				<div></div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		methods:{
			back() {
				this.$router.go(-1)
			  },
				actionSheet() {
				$actionSheet.show({
					title:"",
					theme:"winxin",
					buttons:{
						"":function(){
							alert("")
						},
						"QQ":function() {
							alert("QQ")
						},
						"":function() {
							alert("")
						},
						"":function() {
							alert("")
						},
						"":function() {
							alert("")
						},
						"":function() {
							alert("")
						},
						"":function() {
							alert("")
						}
					},
					cancelText:""
				})
			}
		}
	}
</script>

<style>
	.search{
		z-index: 30;
	}
	.search .inp{
		margin-top: 43px;
		display: flex;
		justify-content: space-between;
		height: 60px;
		line-height: 60px;
		background: white;
		padding: 0 10px;
	}
	.search .inp>div:nth-of-type(1){
		width: 70px;
		height: 100%;
		font-size: 16px;
		text-align: center;
		color: #FF6900;
		cursor: pointer;
	}
	.search .inp>input{
		background: #f6f6f6;
		width: calc(100% - 70px);
		height: 40px;
		margin-top: 10px;
		border-radius: 25px;
		padding: 0 15px;
		cursor: pointer;
	}
	
	.search .bar .buttons {
		font-size: 16px;
		z-index: 10;
	}
	
	.search .bar .buttons:nth-of-type(1) {
		width: 15%;
	}
	.search .bar .buttons:nth-of-type(2){
		width: 8%;
	}
	.search .bar .buttons>span {
		padding: 3px 5px;
		position: relative;
	}
	
	.search .bar .buttons span .dongtai {
		position: absolute;
		width: 50px;
		top: 8px;
		left: 20px;
	}
	
	.search .bar .buttons .icon:nth-of-type(1) {
		font-size: 22px;
		margin-right: 3px;
		position: absolute;
		top: 8px;
	}
	.search .bar .buttons .icon:nth-of-type(2){
		font-size: 22px;
		
		position: absolute;
		top: 5px;
		right: 2px;
	}
	.search .lists>div{
		height: 200px;
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		background: white;
		padding: 10px 16px;
		margin-top: 10px;
	}
	.search .lists>div>div{
		height: 40px;
		width: calc(100% / 3);
		background: #F6F6F6;
		font-size: 15px;
		text-align: center;
		line-height: 40px;
		cursor: pointer;
		color: #888888;
	}
	.search .lists>div>div:nth-of-type(2){
		border-left: 1px solid #CCCCCC;
		border-right: 1px solid #CCCCCC;
	}
	.search .lists>div>div:nth-of-type(5){
		border-left: 1px solid #CCCCCC;
		border-right: 1px solid #CCCCCC;
	}
	.search .lists>div>div:nth-of-type(8){
		border-left: 1px solid #CCCCCC;
		border-right: 1px solid #CCCCCC;
	}
</style>


